const col1Data = [{
  text: '剧毒',
  value: '剧毒'
}, {
  text: '蚂蚁',
  value: '蚂蚁'
}, {
  text: '幽鬼',
  value: '幽鬼'
}]
module.exports = {
  components: {},
  data() {
    return {
      items: [{
        url: 'http://www.didichuxing.com/',
        image: 'http://y.gtimg.cn/music/photo_new/T003R720x288M000002iDhPo41S748.jpg'
      }, {
        url: 'http://www.didichuxing.com/',
        image: 'http://y.gtimg.cn/music/photo_new/T003R720x288M000002z8Lbj3UmMDb.jpg'
      }, {
        url: 'http://www.didichuxing.com/',
        image: 'http://y.gtimg.cn/music/photo_new/T003R720x288M000002tI8QM3jdDZR.jpg'
      }, {
        url: 'http://www.didichuxing.com/',
        image: 'http://y.gtimg.cn/music/photo_new/T003R720x288M000002Wsyay2RR9T2.jpg'
      }]

    }
  },

  created() {

  },
  mounted() {
    this.picker = this.$createPicker({
      title: '单列选择器',
      data: [col1Data],
      onSelect: (selectedText, selectedIndex) => {
        this.$createDialog({
          type: 'warn',
          content: `选中的内容是：${selectedText.join(',')} <br/> 选中的索引是 ${selectedIndex.join(',')}`,
          icon: 'cubeic-alert'
        }).show()
      },
      onCancel: () => {
        this.$createToast({
          type: 'correct',
          txt: 'Picker canceled',
          time: 1000
        }).show()
      }
    })
  },

  methods: {
    changePage(current) {
      console.log('当前轮播图序号为:' + current)
    },
    clickHandle(item, index) {
      console.log(item, index)
    },
    show() {
      const toast = this.$createToast({
        time: 0,
        txt: 'Toast time 0'
      })
      toast.show();
      setTimeout(() => {
        toast.hide()
      }, 20)
    },
    showPicker() {
      this.picker.show()
    }
  }

}



